<template>
  <div class="content">
    <div class="title">新品热卖</div>
    <goods :list="lists"/>
    <div class="more">更多好物，敬请期待</div>
  </div>
</template>

<script>
import goods from "@/web/components/Goods";
export default {
  components: {
    goods
  },
  data() {
    return{
      lists: [
        {
          id: 1,
          name: "俄罗斯美甲（体验价）",
          image: require("@/assets/homepages/img1.png"),
          price_before: "88.00",
          price_current: "88.00",
          btn: "马上抢"
        },
        {
          id: 2,
          name: "蒙巴萨涂抹式去眼袋（体验价）",
          image: require("@/assets/homepages/img2.png"),
          price_before: "19.90",
          price_current: "88.00",
          btn: "马上抢"
        },
        {
          id: 3,
          image: require("@/assets/homepages/img3.png"),
          name: "第八周ALB血清蛋白护肤（体验价）",
          price_before: "50.00",
          price_current: "88.00",
          btn: "马上抢"
        },
        {
          id: 4,
          name: "360身体部位塑性（体验价）",
          image: require("@/assets/homepages/img3.png"),
          price_before: "88.00",
          price_current: "188.00",
          btn: "马上抢"
        }
      ]
    }
    
  }
}
</script>

<style lang="scss" scoped>
.content{
  padding: 0 24px;
  .title{
    height: 80px;
    color: #222;
    font-size:36px;
    padding-top: 25px;
    padding-left: 3px;
    box-sizing: border-box;
  }
}
</style>



